<template lang="pug">
  Section.section_third
    .our-products
      .our-products__text-wrapper
        h2.our-products__title Наша продукция
        a.our-products__link(
          href="#"
        ) Весь ассортимент
      .our-products__slider-wrapper
        flickity(
          ref='flickity'
          :options='flickityOptions'
        )
          .carousel-cell
            .our-products__item.our-products__item_first
              a.our-products__subtitle(
                href="#"
              ) Спирали
              img.our-products__img(src="@/assets/img/section-third/1.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_second
              a.our-products__subtitle(
                href="#"
              ) Рожки
              img.our-products__img(src="@/assets/img/section-third/2.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_third
              a.our-products__subtitle(
                href="#"
              ) Спагетти
              img.our-products__img(src="@/assets/img/section-third/3.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_fourth
              a.our-products__subtitle(
                href="#"
              ) Ракушка
              img.our-products__img(src="@/assets/img/section-third/4.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_fifth
              a.our-products__subtitle(
                href="#"
              ) Бантики
              img.our-products__img(src="@/assets/img/section-third/5.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_sixth
              a.our-products__subtitle(
                href="#"
              ) Домашняя лапша
              img.our-products__img(src="@/assets/img/section-third/6.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_seventh
              a.our-products__subtitle(
                href="#"
              ) Перья
              img.our-products__img(src="@/assets/img/section-third/7.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_eighth
              a.our-products__subtitle(
                href="#"
              ) Спирали
              img.our-products__img(src="@/assets/img/section-third/8.png" alt="")
          .carousel-cell
            .our-products__item.our-products__item_ninth
              a.our-products__subtitle(
                href="#"
              ) Вермишель
              img.our-products__img(src="@/assets/img/section-third/9.png" alt="")
        .our-products__nav
          button.our-products__btn.our-products__btn_prev(
            @click="previous()"
          )
            icon.prev
          button.our-products__btn.our-products__btn_next(
            @click="next()"
          )
            icon.next
</template>

<script>
import Section from '@/layouts/Section'
import Flickity from 'vue-flickity'
import Icon from '@/components/ui/Icon'

export default {
  name: 'SectionThird',
  data () {
    return {
      flickityOptions: {
        initialIndex: 0,
        prevNextButtons: false,
        freeScroll: true,
        contain: true,
        pageDots: false,
        wrapAround: false,
        dragThreshold: 5
      }
    }
  },
  components: {
    Section,
    Flickity,
    Icon
  },
  methods: {
    next () {
      this.$refs.flickity.next()
    },
    previous () {
      this.$refs.flickity.previous()
    }
  }
}
</script>

<style lang="sass">
@import "~@/sass/variables/_core.sass"
@import "~@/sass/mixins/_helpers.sass"

.our-products
  padding: 11vw 0 0 0
  width: 100%
  height: 100%
  z-index: 1
  position: relative
  display: flex
  justify-content: flex-start
  align-items: center
  flex-direction: column
  &__text-wrapper
    position: relative
    margin: 0 0 5.104166666666667vw
  &__title
    @include title
  &__link
    font: 400 26px/1.23 $font-default
    @include fluid-type(768px, 1920px, 14px, 26px)
    color: $color-black-2
    text-decoration: none
    position: absolute
    top: 44px
    right: -50px
    transform: translate3d(100%,0,0)
    opacity: 1
    display: inline-block
    transition: opacity 0.25s ease
    &:hover
      opacity: 0.9
    &:after
      content: ''
      display: block
      width: 100%
      height: 1px
      background: $color-black-2
      position: absolute
  .carousel-cell
    width: 28%
    height: 30.989583333333333vw
    max-height: 595px
    margin-right: 10px
  .flickity-viewport
    overflow: visible !important
  &__slider-wrapper
    width: 100%
    position: absolute
    left: 0
    bottom: 0
    padding: 0 0 0 14.53125vw
  &__item
    display: flex
    justify-content: flex-start
    align-items: center
    flex-direction: column
    height: 100%
    &_first
    &_second
    &_third
      justify-content: space-between
    &_fourth
    &_fifth
    &_sixth
    &_seventh
    &_eighth
    &_ninth
  &__subtitle
    font: 400 26px/1.23 $font-default
    @include fluid-type(768px, 1920px, 14px, 26px)
    color: $color-red
    text-align: center
    position: relative
    text-decoration: none
    padding: 0 0 1px
    &:after
      content: ''
      display: block
      background: $color-red
      width: 100%
      height: 1px
      position: absolute
      left: 0
      bottom: 0
    .our-products__item_first &
      margin: 0 0 3.673vw
    .our-products__item_second &
      margin: 0 0 4.073vw
    .our-products__item_third &
      margin: 0 0 4vw
    .our-products__item_fourth &
      margin: 0 0 4vw
    .our-products__item_fifth &
      margin: 0 0 4vw
    .our-products__item_sixth &
      margin: 0 0 4vw
    .our-products__item_seventh &
      margin: 0 0 4vw
    .our-products__item_eighth &
      margin: 0 0 4vw
    .our-products__item_ninth &
      margin: 0 0 4vw
  &__img
    width: 255px
    height: auto
    display: block
    .our-products__item_first &
      width: 16.953125vw
      max-width: 325.5px
    .our-products__item_second &
      width: 13.28125vw
      max-width: 255px
    .our-products__item_third &
      width: 23.020833333333333vw
      max-width: 442px
    .our-products__item_fourth &
      width: 15.729166666666667vw
      max-width: 302px
    .our-products__item_fifth &
      width: 18.28125vw
      max-width: 351px
    .our-products__item_sixth &
      width: 20.260416666666667vw
      max-width: 389px
    .our-products__item_seventh &
      width: 18.098958333333333vw
      max-width: 347.5px
    .our-products__item_eighth &
      width: 13.28125vw
      max-width: 255px
    .our-products__item_ninth &
      width: 18.020833333333333vw
      max-width: 346px
  &__nav
    width: 69.583333333333333vw
    max-width: 1336px
    position: absolute
    left: 50%
    transform: translate3d(-50%,25px,0)
    bottom: 16.666666666666667vw
    height: 50px
    display: flex
    justify-content: space-between
    align-items: center
  &__btn
    user-select: none
    border: none
    outline: none
    width: 50px
    height: 50px
    position: relative
    border-radius: 25px
    cursor: pointer
    &_prev
    &_next
    .icon
      position: absolute
      left: 50%
      top: 50%
      transform: translate3d(-50%,-50%,0)
      z-index: 1
</style>



