<template lang="pug">
  Section.section_second
    .new-package
      h1.new-package__title Новая удобная пачка
      p.new-package__text
        | Что может быть идеальнее макарон Шебекинских, созданных
        br
        | по&nbsp;итальянским технологиям производства из&nbsp;кристально чистой воды
        br
        | и&nbsp;твердой пшеницы высшего сорта?
      .new-package__package(
          :class="{'new-package__package_open': isActive}"
        )
        img.new-package__package-img.new-package__package-img_closed.new-package__package-img_active(
          src="@/assets/img/section-second/package-close.png" alt=""
          :class="{'new-package__package-img_active': isActive}"
        )
        img.new-package__package-img.new-package__package-img_opened(
          src="@/assets/img/section-second/package-open.png" alt=""
          :class="{'new-package__package-img_active': isActive}"
        )
        .new-package__macaroni(
          :class="{'new-package__macaroni_active': isActive}"
        )
        .new-package__link-wrapper
          .new-package__descr
            | Только макароны
            br
            | Шебекинские
            br
            | в&nbsp;новой удобной пачке!
          .new-package__link.new-package__link_open(
            @click="isActive = !isActive"
          ) {{ showHideText }}
</template>

<script>
import Section from '@/layouts/Section'

export default {
  name: 'SectionSecond',
  data () {
    return {
      isActive: false
    }
  },
  components: {
    Section
  },
  computed: {
    showHideText: function () {
      if (this.isActive) {
        return 'Закрыть'
      }
      return 'Открыть'
    }
  }
}
</script>

<style lang="sass" scoped>
@import "~@/sass/variables/_core.sass"
@import "~@/sass/mixins/_helpers.sass"

.new-package
  padding: 11vw 0 0 0
  width: 100%
  height: 100%
  z-index: 1
  position: relative
  display: flex
  justify-content: flex-start
  align-items: center
  flex-direction: column
  overflow: hidden
  &__title
    @include title
  &__text
    font: 400 26px/1.2307 $font-default
    @include fluid-type(768px, 1920px, 14px, 26px)
    color: $color-black-2
    text-align: center
    width: 100%
    max-width: 910px
    margin: 0 0 3.59375vw
  &__package
    display: flex
    justify-content: center
    align-items: flex-start
    position: absolute
    left: 50%
    bottom: -15vw
    transform: translate3d(-50%,0,0)
    width: 26.927083333333333vw
    height: 27.708333333333333vw
  &__package-img
    display: block
    visibility: hidden
    opacity: 0
    position: absolute
    bottom: 0
    left: 50%
    height: auto
    z-index: 2
    &_closed
      width: 26.927083333333333vw
      max-width: 517px
      transform: translate3d(-50%,0,0)
      transition: transform 0.3s linear, opacity 0.27s ease, visibility 0.27s ease
      opacity: 1
      visibility: visible
      .new-package__package_open &
        transform: translate3d(-50%,20%,0)
        opacity: 0
        visibility: hidden
    &_opened
      width: 29.84375vw
      max-width: 573px
      transform: translate3d(-50%, 20%, 0)
      transition-delay: 0.2s
      transition: transform 0.3s linear, opacity 0.27s ease, visibility 0.27s ease
      //display: none
      .new-package__package_open &
        transform: translate3d(-50%, 42%, 0)
    &_active
      visibility: visible
      opacity: 1
  &__macaroni
    width: 30.189583333333333vw
    max-width: 580.5px
    height: 23.869270833333333vw
    max-height: 458.5px
    position: absolute
    left: 50%
    top: -16vw
    transform: translate3d(calc(-50% - 10px),0,0)
    background-position: 0 0
    background-repeat: repeat
    background-image: url('~@/assets/img/section-second/macaroni.png')
    background-size: contain
    transition-delay: 0.01s
    transition: transform 0.3s ease
    z-index: 1
    &_active
      transform: translate3d(calc(-50% - 10px), -15.84004%, 0)
  &__link-wrapper
    position: absolute
    top: -15.3vw
    right: -55px
    transform: translate3d(100%, 0, 0)
  &__descr
    font: 400 italic 26px/1.23 $font-default
    @include fluid-type(768px, 1920px, 14px, 26px)
    color: $color-black-2
    margin: 0 0 1.197916666666667vw
  &__link
    font: 400 26px/1.23 $font-default
    @include fluid-type(768px, 1920px, 14px, 26px)
    color: $color-red
    text-decoration: underline
    cursor: pointer
    opacity: 1
    display: inline-block
    transition: opacity 0.25s ease
    &:hover
      opacity: 0.9
</style>



