<template lang="pug">
  header.header
    .header__container
      a.header__logo(href="/")
        icon.logo
      .header__list
        a.header__item(
          href="#"
          v-for="item in items"
        ) {{ item.text }}
      .header__list(v-if="0")
        a.header__item(
          href="#"
          @click="goToSection"
          :class="{'header__item_active': isActive}"
        ) Откройте качество по-новому!
        a.header__item(
          href="#"
          @click="goToSection"
          :class="{'header__item_active': isActive}"
        ) Новая удобная пачка
        a.header__item(
          href="#"
          @click="goToSection"
          :class="{'header__item_active': isActive}"
        ) Ассортимент
        a.header__item(
          href="#"
          @click="goToSection"
          :class="{'header__item_active': isActive}"
        ) Шебекино
        a.header__item(
          href="#"
          @click="goToSection"
          :class="{'header__item_active': isActive}"
        ) Контакты
</template>

<script>
import Icon from '@/components/ui/Icon'

export default {
  name: 'Header',
  data () {
    return {
      isActive: false,
      items: [
        {
          id: '0',
          text: 'Откройте качество по-новому!'
        },
        {
          id: '1',
          text: 'Новая удобная пачка'
        },
        {
          id: '2',
          text: 'Ассортимент'
        },
        {
          id: '3',
          text: 'Шебекино'
        },
        {
          id: '4',
          text: 'Контакты'
        }
      ]
    }
  },
  components: {
    Icon
  },
  methods: {
    goToSection: function (event) {
      this.isActive = !this.isActive
    }
  },
  computed: {
    classObject: function () {
      return {
        'header__item_active': this.isActive
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "~@/sass/variables/_core.sass"
@import "~@/sass/mixins/_helpers.sass"

.header
  position: fixed
  width: 100%
  left: 0
  right: 0
  top: 0
  z-index: 2
  &__container
    background: $color-white
    height: 4.21875vw
    width: 100%
    display: flex
    justify-content: center
    align-items: center
    position: relative
    background: $color-white
    border-bottom: 1px solid rgba($color-black-2,0.1)
  &__logo
    text-decoration: none
    position: absolute
    left: 3.4895vw
    top: 1.4375vw
  &__list
    display: flex
    justify-content: center
    align-items: center
  &__item
    font: 500 20px/1.3 $font-default
    @include fluid-type(768px, 1920px, 14px, 20px);
    color: $color-black-2
    text-decoration: none
    position: relative
    margin: 0 1.276vw
    height: 4.21875vw
    overflow: hidden
    display: flex
    justify-content: center
    align-items: center
    &:after
      content: ''
      width: 100%
      height: 2px
      background: $color-red
      position: absolute
      left: 0
      bottom: 0
      transform: translate3d(-101%,0,0)
      transition: all 0.2s ease
    &:hover
      &:after
        transform: translate3d(0,0,0)
    &:last-child
      margin-right: 0
    &:first-child
      margin-left: 0
    &_active
      &:after
        transform: translate3d(0,0,0)
</style>



