<template lang="pug">
  Section.section_fourth
    .about
      h2.about__title Шебекино
      .about__slider-wrapper
        flickity(
          ref='flickity'
          :options='flickityOptions'
        )
          .carousel-cell.carousel-cell_first
            .about__item
              h3.about__subtitle
                | Что такое
                br
                | Шебекинские?
              img.about__img(src="@/assets/img/section-fourth/1.png" alt="")
          .carousel-cell.carousel-cell_second
            .about__item
              .about__text
                | Открывая упаковку &laquo;Шебекинских&raquo;
                br
                | с&nbsp;птичкой, вы&nbsp;открываете не&nbsp;просто
                br
                | упаковку вкусных макарон по&nbsp;доступной
                br
                | цене, созданных в&nbsp;России, а&nbsp;
                span.about__text-bold продукт,
                  br
                  | соответствующий лучшим образцам
                  br
                  | макарон в&nbsp;мире.
          .carousel-cell.carousel-cell_third
            .about__item
              img.about__img(src="@/assets/img/section-fourth/2.png" alt="")
              .about__tip
                | Так зерно поступает
                br
                | на&nbsp;линию производства
          .carousel-cell.carousel-cell_fourth
            .about__item
              .about__subtitle
                | Как мы&nbsp;этого
                br
                | добились?
          .carousel-cell.carousel-cell_fifth
            .about__item
              .about__row
                .about__text
                  | Самое главное&nbsp;&mdash; это ингредиенты.
                  br
                  | Для создания &laquo;Шебекинских&raquo;
                  br
                  | используется только отборная пшеница
                  br
                  | твердых сортов и&nbsp;чистейшая питьевая
                  br
                  | вода, потому что используя только такие
                  br
                  | компоненты можно создать качественный,
                  br
                  | вкусный и&nbsp;полезный продукт.
                .about__text
                  | Мы&nbsp;намеренно не&nbsp;выращиваем
                  br
                  | зерно самостоятельно, а&nbsp;обладаем
                  br
                  | уникальными специалистами
                  br
                  | и&nbsp;технологиями, что позволяет нам
                  br
                  | отбирать идеальные, экологически
                  br
                  | чистые партии для помола в&nbsp;России.
              .about__row
                img.about__img(src="@/assets/img/section-fourth/4.png" alt="")
          .carousel-cell.carousel-cell_sixth
            .about__item
              .about__subtitle
                | Уникальные
                br
                | технологии производства
                br
                | и&nbsp;контроля качества
          .carousel-cell.carousel-cell_seventh
            .about__item
              img.about__img(src="@/assets/img/section-fourth/5.png" alt="")
              .about__tip
                | Комната трясётся.
                br
                | Хорошо бы узнать,
                br
                | что это такое!
          .carousel-cell.carousel-cell_eighth
            .about__item
              .about__text
                | Мы&nbsp;не&nbsp;только используем весь
                br
                | иностранный опыт, итальянское
                br
                | оборудование от&nbsp;мировых лидеров в&nbsp;этой
                br
                | области, но&nbsp;и&nbsp;обладаем
                span.about__text-bold собственными&nbsp;
                  br
                  | &nbsp;уникальными технологиями
                | по&nbsp;контролю&nbsp;
                br
                | качества и&nbsp;производству:
          .carousel-cell.carousel-cell_ninth
            .about__item
              img.about__img(src="@/assets/img/section-fourth/7.png" alt="")
              .about__text.about__text_white
                span.about__text-bold Немеханическая система замеса
                  br
                  | теста&nbsp;
                | позволяет сохранить белки
                br
                | твёрдого зерна&nbsp;&mdash; основную суть
                br
                | качественных макарон.
          .carousel-cell.carousel-cell_tenth
            .about__item
              img.about__img(src="@/assets/img/section-fourth/8.png" alt="")
              .about__text.about__text_white
                span.about__text-bold Полная оптическая сортировка&nbsp;
                | &mdash;
                br
                | позволяет выявлять даже скрытые
                br
                | дефекты, определяет содержание
                br
                | белка и&nbsp;микроэлементов.
          .carousel-cell.carousel-cell_eleventh
            .about__item
              .about__subtitle
                | Современное
                br
                | производство
          .carousel-cell.carousel-cell_twelfth
            .about__item
              .about__text
                | На&nbsp;сегодняшний день макароны
                br
                | Шебекинские производятся на&nbsp;одних
                br
                | из&nbsp;самых современных фабрик, одна
                br
                | из&nbsp;которых включает в&nbsp;себя мельницу,
                br
                | полностью покрывающую потребности
                br
                | всех наших производств.
              .about__text.about__text_white
                | Макаронная фабрика&nbsp;
                span.about__text-bold (300 тонн в&nbsp;сутки)
                br
                | с&nbsp;мельницей по&nbsp;помолу твердого зерна
                br
                span.about__text-bold (400 тонн зерна/сутки)&nbsp;
                | и&nbsp;примельничным&nbsp;
                br
                | элеватором объемом хранения&nbsp
                span.about__text-bold 8&nbsp;000&nbsp;тыс.
                  br
                  | тонн зерна.
          .carousel-cell.carousel-cell_thirteenth
            .about__item
              .about__row
                img.about__img(src="@/assets/img/section-fourth/9.png" alt="")
                .about__text.about__text_white
                  | Декортикаторы зерна, система бережной сушки, самая современная система очистки воды для замеса теста и&nbsp;другие технологии.
          .carousel-cell.carousel-cell_fourteenth
            .about__item
              .about__text
                | Все процессы на&nbsp;наших фабриках полностью автоматизированы и&nbsp;контролируются с&nbsp;помощью самого передового программного обеспечения. Начиная с&nbsp;первого этапа складирования зерна и&nbsp;заканчивая погрузкой готовой продукции.
              .about__text
                | Длинные и&nbsp;короткие, для супа, гарнира, запекания, для особого случая и&nbsp;для детей, даже цветные с&nbsp;натуральными добавками шпината и&nbsp;томата&nbsp;&mdash; каждый найдет себе Шебекинские по&nbsp;вкусу, потому что в&nbsp;линейке их&nbsp;более&nbsp;30.
          .carousel-cell.carousel-cell_fifteenth
            .about__item
              img.about__img(src="@/assets/img/section-fourth/10.png" alt="")
              .about__text.about__text_white
                | В&nbsp;2016 году спагетти &laquo;Шебекенские&raquo; получили престижную российскую награду &laquo;Знак качества&raquo;.
        .about__nav
          button.about__btn.about__btn_prev(
            @click="previous()"
          )
            icon.prev
          button.about__btn.about__btn_next(
            @click="next()"
          )
            icon.next
</template>

<script>
import Section from '@/layouts/Section'
import Flickity from 'vue-flickity'
import Icon from '@/components/ui/Icon'

export default {
  name: 'SectionFourth',
  data () {
    return {
      flickityOptions: {
        initialIndex: 0,
        prevNextButtons: false,
        freeScroll: true,
        contain: true,
        pageDots: false,
        wrapAround: false,
        dragThreshold: 5
      }
    }
  },
  components: {
    Section,
    Flickity,
    Icon
  },
  methods: {
    next () {
      this.$refs.flickity.next()
    },
    previous () {
      this.$refs.flickity.previous()
    }
  }
}
</script>

<style lang="sass">
@import "~@/sass/variables/_core.sass"
@import "~@/sass/mixins/_helpers.sass"

.about
  padding: 11.8vw 0 0 0
  width: 100%
  height: 100%
  z-index: 1
  position: relative
  display: flex
  justify-content: flex-start
  align-items: center
  flex-direction: column
  &__title
    @include title
    //line-height: 1;
  &__subtitle
    @include subtitle
    .carousel-cell_first &
      margin: 0 0 2.5vw 1vw
    .carousel-cell_fourth &
    .carousel-cell_sixth &
  &__slider-wrapper
    width: 100%
    position: absolute
    left: 0
    bottom: 0
    padding: 0 0 0 36.5vw
  .carousel-cell
