      width: 60vw
      padding: 4.8vw 0 0 0
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
    &_sixth
      max-width: 684px
      width: 43vw
      padding: 4.1vw 0 0 0
    &_seventh
      max-width: 545px
      width: 22vw
      padding: 1.86vw 0 0 0
    &_eighth
      max-width: 575px
      width: 30.6vw
      padding: 4.8vw 0 0 0
    &_ninth
      max-width: 460px
      width: 23.958333333333333vw
      padding: 1.86vw 0 0 0
      margin-right: 20px
    &_tenth
      max-width: 550px
      width: 28.645833333333333vw
      padding: 1.86vw 0 0 0
    &_eleventh
      max-width: 404px
      width: 21.041666666666667vw
      padding: 4.1vw 0 0 0
    &_twelfth
      max-width: 575px
      width: 30.6vw
      padding: 4.8vw 0 0 0
    &_thirteenth
      max-width: 550px
      width: 28.645833333333333vw
      padding: 1.86vw 0 0 0
    &_fourteenth
      max-width: 1135px
      width: 59.114583333333333vw
      padding: 4.8vw 0 0 0
      margin-right: 40px
    &_fifteenth
      max-width: 363px
      width: 18.90625vw
      padding: 3.05vw 0 0 0
  .flickity-viewport
    overflow: visible !important
  &__item
    height: 100%
    .carousel-cell_fourteenth &
      display: flex
      justify-content: space-between
      align-items: flex-start
    // .carousel-cell_first &
      // display: flex
      // justify-content: flex-start
      // align-items: center
      // flex-direction: column
  &__img
    height: auto
    display: block
    .carousel-cell_first &
      max-width: 396px
      width: 20.625vw
    .carousel-cell_third &
      max-width: 460px
      width: 23.958333333333333vw
      margin: 0 0 1.5vw
    .carousel-cell_fifth &
      max-width: 1226.32px
      width: 63.87083333333333vw
      transform: translate3d(-6.5vw,0.9vw,0)
    .carousel-cell_seventh &
      max-width: 460.15px
      width: 23.966145833333333vw
      transform: translate3d(-6.5vw,0,0)
    .carousel-cell_ninth &
      max-width: 460px
      width: 23.958333333333333vw
      transform: translate3d(0,0,0)
      margin: 0 0 6.71875vw
    .carousel-cell_tenth &
      max-width: 460px
      width: 23.958333333333333vw
      transform: translate3d(0,0,0)
      margin: 0 0 6.71875vw
    .carousel-cell_thirteenth &
      max-width: 460px
      width: 23.958333333333333vw
      transform: translate3d(0,0,0)
      margin: 0 0 6.71875vw
    .carousel-cell_fifteenth &
      max-width: 251.23px
      width: 13.084895833333333vw
      transform: translate3d(0,0,0)
      margin: 0 0 6.71875vw
