    @include text
    &_white
      color: $color-white
    .carousel-cell_second &
    .carousel-cell_fifth &
      &:first-child
        margin: 0 4vw 0 0
    .carousel-cell_eighth &
    .carousel-cell_twelfth &:first-child
      margin: 0 0 9vw
    .carousel-cell_fifteenth
      transform: translate3d(0.885416666666667vw, 0.885416666666667vw, 0)
  &__text-bold
    font-weight: bold
  &__tip
    font: 400 italic 26px/1.230769230769231 $font-default
    @include fluid-type(768px,1920px,12px,26px)
    color: $color-white
    position: relative
    &:before
      content: ''
      display: block
    .carousel-cell_third &
      position: absolute
      top: 19vw
      left: -1.2vw
      padding: 6vw 0 0 1.4vw
      &:before
        max-width: 51px
        width: 2.65625vw
        max-height: 93px
        height: 4.84375vw
        background-image: url("~@/assets/img/section-fourth/3.png")
