<template lang="pug">
  i.icon
</template>

<script>

export default {
  name: 'Icon'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
@import "~@/sass/mixins/svg/_core.sass"

.icon
  display: block
  background-position: center center
  background-repeat: no-repeat
  background-size: contain
.mouse
  @include mouse
  width: 1.666666666666667vw
  height: 2.604166666666667vw
  background-size: contain
.logo
  @include logo
  width: 8.221vw
  height: 5.833vw
.sound
  @include sound
  width: 24px
  height: 24px
.nosound
  @include nosound
  width: 24px
  height: 24px
.fullscreen
  @include fullscreen
  width: 19px
  height: 19px
.prev
  @include prev
  width: 21px
  height: 22px
.next
  @include next
  width: 21px
  height: 22px
</style>



