<template lang="pug">
  .video
    video-player(
      class="video__player"
      :options="videoOptions"
    )
    .video__mouse
      icon.mouse
</template>

<script>
import 'video.js/dist/video-js.css'
import '@/components/video/video-js.sass'
import { videoPlayer } from 'vue-video-player'
import Icon from '@/components/ui/Icon'

export default {
  name: 'Video',
  data () {
    return {
      videoOptions: {
        autoplay: false,
        controls: true,
        muted: false,
        language: 'ru',
        sources:
        [{
          src: require('@/assets/video/ad.mp4'),
          type: 'video/mp4'
        }],
        fluid: true,
        width: '100%',
        setVolume: '100%',
        inactivityTimeout: 0
      }
    }
  },
  components: {
    videoPlayer,
    Icon
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
@import "~@/sass/variables/_core.sass"
@import "~@/sass/mixins/_helpers.sass"

.video
  padding: 8.59375vw 0 0 0
  width: 100%
  height: 100%
  z-index: 1
  position: relative
  display: flex
  justify-content: flex-start
  align-items: center
  flex-direction: column
  &__player
    width: 69.479vw
    height: 38.125vw
    z-index: 1
  &__mouse
    position: relative
    top: calc(12px + 3.229166666666667vw)
</style>



