<template lang="pug">
  MainLayout
    .home
      hooper(
        :settings="hooperSettings"
      )
        slide
          SectionFirst
        slide
          SectionSecond
        slide
          SectionThird
        slide
          SectionFourth
</template>

<script>
import MainLayout from '@/layouts/MainLayout'
import SectionFirst from '@/components/sections/Section1.vue'
import SectionSecond from '@/components/sections/Section2.vue'
import SectionThird from '@/components/sections/Section3.vue'
import SectionFourth from '@/components/sections/Section4.vue'
import { Hooper, Slide } from 'hooper'
import './hooper.sass'

export default {
  name: 'Home',
  data () {
    return {
      hooperSettings: {
        itemsToShow: 1,
        centerMode: false,
        vertical: true
      }
    }
  },
  components: {
    MainLayout,
    SectionFirst,
    SectionSecond,
    SectionThird,
    SectionFourth,
    Hooper,
    Slide
  }
}
</script>

<style lang="sass">
  @import "~@/sass/variables/_core.sass"
  @import "~@/sass/mixins/_helpers.sass"
  .home
    width: 100%
    height: 100vh
    overflow: hidden
    position: relative
    background-color: $color-white
    &:before
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      left: 0
      top: 0
      opacity: 0.09
      background-position: 0 0
      background-repeat: repeat
      background-image: url('~@/assets/img/bgs/bg-1.png')
      background-size: 192px 192px
</style>



