import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import Assortment from '@/pages/Assortment'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/assortment',
      name: 'Assortment',
      component: Assortment
    }
  ]
})



